import {ChangeDetectorRef, Component, NgZone, OnInit} from '@angular/core';
import {AppService} from '../app.service';
import {$TAB} from 'codelyzer/angular/styles/chars';

declare var $: any;
declare var CKEDITOR: any;

@Component({
  selector: 'app-edit-mode',
  templateUrl: './edit-mode.component.html',
  styleUrls: ['./edit-mode.component.css']
})
export class EditModeComponent implements OnInit {

  /*MAIN VARIABLES*/

  mainObj: any = null;
  questionMove = null;
  questionOrigin = null;
  moveAll = null;
  questionSelected = null;
  relationSelected = null;
  relPreview = null;
  questionPreview = null;

  relationsSelected = [];

  /*STATUS VARIABLES*/

  mouseIsDown = false;
  shiftIsDown = false;

  constructor(public service: AppService, private changes: ChangeDetectorRef, private zone: NgZone) { }

  ngOnInit() {

      CKEDITOR.replace('textarea');

      if(!window.localStorage.getItem('w25_obj') || this.mainObj && !this.mainObj.questions.length){
        this.startObj();
      }
      this.getObject();
      this.startListeners();
      this.changes.detectChanges();

      $('#modalinstructions').modal('show');
  }

  /*START OBJ ON LOCALSTORAGE*/

  startObj(){

      let block = $('#area-edit')[0];

      let halfWidth = parseInt(block.offsetWidth)/2;
      let halfHeight = parseInt(block.offsetHeight)/2;

      let obj = {
        questions: [
          {
            id: 1,
            content: '',
            start: true,
            end: false,
            x: halfWidth - 200,
            y: halfHeight - 50
          },
          {
            id: 2,
            content: '',
            start: false,
            end: true,
            x: halfWidth + 100,
            y: halfHeight - 50
          }
        ],
        relations: [
          // {
          //   question1: 1,
          //   question2: 2,
          //   answer1: true,
          //   answer2: false,
          //   correct: true,
          //   content: ''
          // }
        ]
      };

      this.service.setObj(obj);

  }

  /*OBJECT FUNCTIONS*/

  getObject(){
    this.mainObj = this.service.getObj();
  }

  getQuestionFromId(id){
    return this.mainObj.questions.find(i => i.id == id);
  }

  updateObj(){
    this.service.setObj(this.mainObj);
  }

  /*SETTERS*/

  setQuestionMove(q){
    if(!this.shiftIsDown){
      this.questionMove = q;
    }else{
      if(!this.questionOrigin){
        this.questionOrigin = q;
        this.generateNewQuestion(this.questionOrigin);
      }
    }
  }

  setQuestionSelected(q){
    if(q && q.id == 2) return false;
    this.questionSelected = q;
    if(q) this.getRelationsOfSelection();
  }

  setRelationSelected(q){
    this.relationSelected = q;
  }

  /*GETTERS*/

  getMiddleX(rel){
      let slide1 = this.getQuestionFromId(rel.question1);
      let slide2 = this.getQuestionFromId(rel.question2);

      return slide1.x+((slide2.x-slide1.x)/2)+50;
  }

  getMiddleY(rel){
    let slide1 = this.getQuestionFromId(rel.question1);
    let slide2 = this.getQuestionFromId(rel.question2);

    return slide1.y+((slide2.y-slide1.y)/2)+25;
  }

  getAngleBetweenTwoPoints(rel){
    let q1 = this.getQuestionFromId(rel.question1);
    let q2 = this.getQuestionFromId(rel.question2);
    return Math.atan2(q2.y - q1.y, q2.x - q1.x) * 180 / Math.PI;
  }

  getDistanceOfRel(rel){
    let q1 = this.getQuestionFromId(rel.question1);
    let q2 = this.getQuestionFromId(rel.question2);
    return  Math.sqrt(((q2.x - q1.x)*(q2.x - q1.x))+((q2.y - q1.y)*(q2.y - q1.y)));
  }

  /*LISTENERS*/

  startListeners(){

    $('#exampleModal').on('show.bs.modal', e => {
        CKEDITOR.instances.textarea.setData(this.questionSelected.content);
    });

    $('#inputImport').on('change', e => {

        let file = e.target.files[0];
        let reader = new FileReader();
        reader.onload = e => {
            let result: any = reader.result;
            this.service.setObj(JSON.parse(result));
            this.mainObj = this.service.getObj();
            this.updateObj();
        }
        reader.readAsText(file);

    });

    $('#exampleModal').on('hide.bs.modal', e => {
        let data = CKEDITOR.instances.textarea.getData();
        this.questionSelected.content = data;
        this.updateObj();
        this.setQuestionSelected(null);
    });

    $(document).on('click', e => {
      if(e.target.id == 'area-edit'){
        this.setQuestionSelected(null);
        this.setRelationSelected(null);
      }
    });

    $(document).on('mousemove', e => {
      if(this.questionMove && !this.moveAll && !this.questionSelected){

        let block = $('#area-edit')[0];

        let x = (e.pageX - block.offsetLeft)-50;
        let y = (e.pageY - block.offsetTop)-50;

        this.questionMove.x = x;
        this.questionMove.y = y;

        this.updateObj();

      }

      if(this.questionPreview){
        if(!e.target.classList.contains('preview') && e.target.classList.contains('item-question')){


          $('.item-question.preview').addClass('invisibles');
        }else{

          // $('.item-question.preview').css({
          //   'background-color': '#5d8a92'
          // });
          $('.item-question.preview').removeClass('invisibles');
        }
        let block = $('#area-edit')[0];

        let x = (e.pageX - block.offsetLeft)-50;
        let y = (e.pageY - block.offsetTop)-50;

        this.questionPreview.x = x;
        this.questionPreview.y = y;
      }

      if(this.moveAll){

        this.mainObj.questions.map(q => {

          q.x += e.pageX-this.moveAll.x;
          q.y += e.pageY-this.moveAll.y;

        });

        this.moveAll = {
          x: e.pageX,
          y: e.pageY
        };

      }
    });

    $(document).on('mousedown', e => {
        this.mouseIsDown = true;

        if(e.target.id == 'area-edit' && this.shiftIsDown){

          this.moveAll = {
            x: e.pageX,
            y: e.pageY
          };

        }

    });

    $(document).on('mouseup', e => {
      this.mouseIsDown = false;
      this.moveAll = null;

      this.setQuestionMove(null);
      this.relPreview = null;
      if(this.questionPreview) this.questionPreview.preview = false;
      this.questionPreview = null;

      if(this.shiftIsDown && e.target && e.target.id == 'area-edit'){
        let block = $('#area-edit')[0];
      }

      if(this.questionOrigin && this.shiftIsDown && e.target.classList.contains('item-question') && !e.target.classList.contains('preview')){
          let toSlide = this.getQuestionFromId(e.target.dataset.questionid);
          this.generateNewRelation(this.questionOrigin, toSlide);
      }

      this.questionOrigin = null;

      this.updateObj();

    });

    $(document).on('keydown', e => {
      if(e.which == 16){
        this.shiftIsDown = true;
      }
    });

    $(document).on('keyup', e => {
      if(e.which == 16){
        this.shiftIsDown = false;
      }
      if(e.which == 46){
        this.deleteQuestion();
        this.deleteRelation();
      }
    });

  }

  generatePreviewQuestion(){
    let lastId = this.mainObj.questions[this.mainObj.questions.length-1].id;
    let newId = lastId+1;
    this.questionPreview = {
      id: newId,
      content: '',
      start: false,
      end: false,
      x: this.questionOrigin.x,
      y: this.questionOrigin.y,
      preview: false
    };
  }

  // GENERATES A NEW RELATION

  generateNewRelation(q1, q2){
    let rel = this.mainObj.relations.find(r => r.question1 == q1.id && r.question2 == q2.id);
    if(rel) return false;

    if(q1 && q2){
      let objRelation = {
        question1: q1.id,
        question2: q2.id,
        answer1: true,
        answer2: false,
        correct: false,
        content: ''
      };
      this.mainObj.relations.push(objRelation);
      this.deleteQuestion(this.mainObj.questions[this.mainObj.questions.length-1]);
      this.updateObj();
    }
  }

  // GENERATES A NEW QUESTION

  generateNewQuestion(questionOrigin){
    if(this.questionOrigin){
      let lastId = this.mainObj.questions[this.mainObj.questions.length-1].id;
      let newId = lastId+1;
      let newQuestion = {
        id: newId,
        content: '',
        start: false,
        end: false,
        x: questionOrigin.x,
        y: questionOrigin.y,
        preview: true,
      };

      this.questionPreview = newQuestion;

      let relation =  {
        question1: questionOrigin.id,
        question2: newQuestion.id,
        answer1: true,
        answer2: false,
        correct: false,
        content: ''
      };

      this.mainObj.questions.push(newQuestion);

      setTimeout(() => {
        this.mainObj.relations.push(relation);
        this.updateObj();
        // this.questionOrigin = null;
      }, 300);
    }
  }

  // TOGGLE THE DIRECTION OF A LINK

  toggleDirectionAnswer(rel){
    let question1 = rel.question1;
    rel.question1 = rel.question2;
    rel.question2 = question1;
    this.updateObj();
  }

  // OPEN MODAL EDITION

  openModal(){
    $('#exampleModal').modal('show');
  }

  // GET RELATIONS OF QUESTION SELECTED

  getRelationsOfSelection(){
    this.relationsSelected = this.mainObj.relations.filter(r => r.question1 == this.questionSelected.id && r.answer1);
  }

  //TURN A ANSWER RIGHT

  turnRight(event, relation){
    relation.correct = event;
    this.updateObj();
  }

  // DELETES

  deleteQuestion(question = null){
    if(!question && this.questionSelected){
      if(this.questionSelected.id == 1 || this.questionSelected.id == 2) return false;
      this.zone.run(() => {
        this.mainObj.relations = this.mainObj.relations.filter(r => r.question1 != this.questionSelected.id && r.question2 != this.questionSelected.id);
        this.mainObj.questions = this.mainObj.questions.filter(q => q != this.questionSelected);
        this.setQuestionSelected(null);
        this.updateObj();
      });
    }else{
      this.zone.run(() => {
        this.mainObj.relations = this.mainObj.relations.filter(r => r.question1 != question.id && r.question2 != question.id);
        this.mainObj.questions = this.mainObj.questions.filter(q => q != question);
        this.setQuestionSelected(null);
        this.updateObj();
      });
    }
  }

  deleteRelation(){
    if(this.relationSelected){
      this.zone.run(() => {
        this.mainObj.relations = this.mainObj.relations.filter(r => r != this.relationSelected);
        this.setRelationSelected(null);
        this.updateObj();
      });
    }
  }

  export(){
    let data = `data:text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(this.service.getObj()))}`;
    let a = document.createElement('a');
    a.setAttribute('href', data);
    a.setAttribute('download', 'data.json');
    a.click();
  }

}
