import { Component, OnInit } from '@angular/core';
import {AppService} from '../app.service';

declare var $: any;

@Component({
  selector: 'app-view-mode',
  templateUrl: './view-mode.component.html',
  styleUrls: ['./view-mode.component.css']
})
export class ViewModeComponent implements OnInit {

  // MAIN VARIABLES

  objQuestions: any = [];
  relationsSelected = [];

  actualQuestion = null;
  nextQuestion = null;

  wrongs = [];
  corrects = [];
  wrongsTryings = 0;
  correctsTryings = 0;
  questionsFound = [];

  showStatiscs = false;


  constructor(public service: AppService) { }

  // INIT ACTIONS

  ngOnInit() {
    document.querySelector('.block-view').requestFullscreen();
    this.getObjPresentation();
    this.actualQuestion = this.objQuestions.questions.find(q => q.id == 1);
    this.getAnswers();
    this.questionsFound.push(this.actualQuestion);
  }

  // GET OBJECT FROM SERVICE

  getObjPresentation(){
    this.objQuestions = this.service.getObj();
  }

  // GET ANSWERS LIST

  getAnswers(){
    if(this.actualQuestion.id == 2){
      this.relationsSelected = [];
      this.mountStatistcs(true);
    }else{
      this.relationsSelected = this.objQuestions.relations.filter(r => r.question1 == this.actualQuestion.id && r.answer1);
    }
  }

  // ANSWER A QUESTION METHOD

  testQuestion(event, rel){
      event.target.classList.remove('btn-secondary');
      let next = this.objQuestions.questions.find(q => q.id == rel.question2);
      if(!this.questionsFound.find(c => c == next)){
        this.questionsFound.push(this.objQuestions.questions.find(q => q.id == rel.question2));
      }
      if(rel.correct){
        event.target.classList.add('correctAnimation');
        event.target.classList.add('btn-success');
        if(!this.corrects.find(c => c == rel)){
          this.corrects.push(rel);
        }
        this.correctsTryings++;
      }else{
        if(!this.wrongs.find(c => c == rel)){
          this.wrongs.push(rel);
        }
        this.wrongsTryings++;
        event.target.classList.add('wrongAnimation');
        event.target.classList.add('btn-danger');
      }

      setTimeout(() => {

        this.nextQuestion = this.objQuestions.questions.find(q => q.id == rel.question2);
        $('.nextQuestion').addClass('slideFromRight');
        $('.actualQuestion').addClass('slideToLeft');

        setTimeout(() => {
          this.actualQuestion = this.nextQuestion;
          this.nextQuestion = null;
          $('.nextQuestion').removeClass('slideFromRight');
          $('.actualQuestion').removeClass('slideToLeft');
          this.getAnswers();
        }, 1000);

      }, 320);
  }

  mountStatistcs(val){
      this.showStatiscs = val;
  }

  // RESTART METHOD

  restart(){
    this.wrongs = [];
    this.corrects = [];
    this.wrongsTryings = 0;
    this.correctsTryings = 0;
    this.questionsFound = [];
    this.showStatiscs = false;
    this.actualQuestion = this.objQuestions.questions.find(q => q.id == 1);
    this.questionsFound.push(this.actualQuestion);
    this.getAnswers();
  }

}
